import React from "react";
import Category from "../Category";
import "./style.css";

function index({ categories, nextSlide, prevSlide }) {
  return (
    <div className="categories">
      {categories?.map(({ title, subtitle, img, decs, slug, price, items }) => (
        <Category
          key={slug}
          title={title}
          img={img}
          subtitle={subtitle}
          decs={decs}
          slug={slug}
          price={price}
          items={items}
          nextSlide={nextSlide}
          prevSlide={prevSlide}
        />
      ))}
    </div>
  );
}

export default index;
