import React, { useEffect } from "react";
import { Link } from "react-router-dom";
import { FaCheckDouble } from "react-icons/fa";
import "./style.css";
import ImgSlider from "../../../component/ImgSlider/ImgSlider";

function Category({ title, img, subtitle, decs, slug, price, items }) {
  useEffect(() => {
    const categories = document.querySelectorAll(".category__item");
    const options = { threshold: 0.4 };

    const preloadImg = (img) => {
      const src = img.getAttribute("data-src");
      img.src = src;
    };

    const observer = new IntersectionObserver((entries) => {
      entries.forEach((entry) => {
        if (!entry.isIntersecting) {
          return;
        } else {
          entry.target.classList.add("fade-in");
          // const img = entry.target.querySelector("[data-src]");
          // preloadImg(img);
          observer.unobserve(entry.target);
        }
      });
    }, options);

    categories.forEach((category) => {
      observer.observe(category);
    });
  }, []);

  return (
    <>
      <div className="category__item">
        <div className="category__left">
          <ImgSlider img={img} />
        </div>
        <div className="category__right">
          <h1>{title}</h1>
          {subtitle && <h4>{subtitle}</h4>}
          {decs && <p>{decs}</p>}
          {items && (
            <ul>
              {items.map((item, idx) => (
                <li key={idx}>{item}</li>
              ))}
            </ul>
          )}
          <div className="category__links">
            {price && (
              <h5>
                Rooms From <span>Ksh.{price}</span>
              </h5>
            )}
            {slug && (
              <Link className="fancy-link" to={slug}>
                {title === "facilities" ? "Read More" : "Discover"}
              </Link>
            )}
          </div>
        </div>
      </div>
    </>
  );
}

export default Category;
