import React from "react";
import { Link } from "react-router-dom";
import { MdChevronRight } from "react-icons/md";
import "./style.css";
import logo from "../../assets/imgs/logo.jpg";
import { useLocation } from "react-use";

function Footer({ links, socials }) {
  const { pathname } = useLocation();

  return (
    <footer id="footer">
      <div
        className="footer"
        initial={{ y: "50%", opacity: 0 }}
        animate={{ y: 0, opacity: 1 }}
        transition={{ duration: 1, delay: 1 }}>
        <div className="footer__logo">
          <img src={logo} alt="Logo" />
        </div>
        <div className="footer__content">
          <div className="footer__item">
            <h2>Contact Details</h2>
            <div className="footer__location">
              <p>Kababa Resort &amp; Restaurant</p>
              <p>P.O Box 144-40600</p>
              <p>Siaya</p>
            </div>
            <div className="footer__mobile">
              <p>T:(+254) 795772333 /</p>
              <p>(+254) 755152421</p>
            </div>
            <div className="footer__email">
              <p>E: brianmauriceoluoch@gmail.com</p>
            </div>
          </div>
          <div className="footer__item">
            <h2>Kababa Hotel</h2>
            <ul>
              {links?.map(({ link, route }, idx) => (
                <li key={idx}>
                  <Link
                    className={`navlinks ${pathname === route && "active"}`}
                    to={route}>
                    {link}
                  </Link>
                </li>
              ))}
            </ul>
          </div>
          <div className="footer__item">
            <div className="footer__subscribe">
              <h2>Subscribe To Our Newsletter</h2>
              <form>
                <div className="input__group">
                  <input type="email" placeholder="Email address" />
                  <MdChevronRight />
                </div>
              </form>
            </div>
            <div className="footer__socials">
              <h2>Follow Us</h2>
              <ul>
                {socials?.map(({ icon, link }, idx) => (
                  <li key={idx}>
                    <a href={link} target="_blank" rel="noopener noreferrer">
                      {icon}
                    </a>
                  </li>
                ))}
              </ul>
            </div>
          </div>
        </div>
      </div>
    </footer>
  );
}

export default Footer;
