import React from "react";
import {
  MdTv,
  MdWifi,
  MdRestaurant,
  MdAlarm,
  MdLocalParking,
  MdCameraAlt,
} from "react-icons/md";
import {
  FaWineGlass,
  FaMugHot,
  FaCoffee,
  FaWifi,
  FaCocktail,
  FaSwimmingPool,
  FaTruckLoading,
  FaUserSecret,
  FaMapMarkedAlt,
  FaPlane,
} from "react-icons/fa";
import "./style.css";
import Nav from "../../component/Nav/Nav";

function Amenities() {
  const roomAmenities = [
    {
      icon: <MdTv />,
      title: "Widescreen LCD TV",
      desc: `Enjoy your favorite TV shows or 
      sports when you're at your room resting on a 40" TV screen.`,
    },
    {
      icon: <FaWineGlass />,
      title: "Wine bottle",
      desc: `Have a complementary bottle 
      of wine free, when you stay for more than 5 nights at Kababa Resort.`,
    },
    {
      icon: <FaCoffee />,
      title: "Coffeemaker",
      desc: `Start your morning with the highest quality coffee made-in-room with 
      Lavazza coffeemaker.`,
    },
    {
      icon: <FaWifi />,
      title: "Free Wi-Fi",
      desc: `Rest and do some work if you need to, high speed wi-fi 
      internet is free all around the hotel premises.`,
    },
    {
      icon: <FaCocktail />,
      title: "Everyday towel changes?",
      desc: `Room staff will change the towels everyday if you prefer to. Just leave them in the bath.`,
    },
    {
      icon: <FaCocktail />,
      title: "Cocktails?",
      desc: `If you're a fan of cocktails - our bartebders will make if for you. Just 
      visit the lobby.`,
    },
    {
      icon: <MdAlarm />,
      title: "Alarm clock",
      desc: `If you're in a need to stay up early (but we hardly suspect it), our 
      alarm clocks will take care of that.`,
    },
    {
      icon: <FaCocktail />,
      title: "Premium bathtubs?",
      desc: `Jacuzzi or a regular bath tub? It's  your choice, so whiever you prefer 
      let us know when booking.`,
    },
    {
      icon: <MdAlarm />,
      title: "Alarm clock",
      desc: `Need to get up early? Not really, just to come down on breakfast. No worries, we've got you covered.`,
    },
  ];

  const hotelAmenities = [
    {
      icon: <FaSwimmingPool />,
      title: "Indoor and outdoor pool",
      desc: `Our pools are always open, whether at night or daytime. You just 
      decide which one you want.`,
    },
    {
      icon: <FaTruckLoading />,
      title: "Baggage carriers",
      desc: `Our staff will gladly help you out with 
      your baggage and carry them into your room.`,
    },
    {
      icon: <FaTruckLoading />,
      title: "Clothes cleaning",
      desc: `Need to wash your clothes? No worries! 
      Our cleaning services will help you out for a small fee.`,
    },
    {
      icon: <FaUserSecret />,
      title: "Privacy is essential",
      desc: `Whether you just want a quiet nap or value your privacy, just let us 
      know. Noone will bother you.`,
    },
    {
      icon: <MdLocalParking />,
      title: "Covered parking lot",
      desc: `Your car will be safe and sound on our covered parking lot. You can additionally pay for garage.`,
    },
    {
      icon: <FaMapMarkedAlt />,
      title: "Guide",
      desc: `Our professional guides will be there to help you when 
      hiking on unmarked places.`,
    },
    {
      icon: <MdRestaurant />,
      title: "Room Restaurant",
      desc: `Our professional guides will be there to help you when 
      hiking on unmarked places.`,
    },
    {
      icon: <FaPlane />,
      title: "Airport transport",
      desc: `If you didn't come in by your car or by bus, we can drive 
      you with out shuttle to the airport.`,
    },
    {
      icon: <MdCameraAlt />,
      title: "Photographer",
      desc: `Capture your best moments on your vacation by having our professional photographer with you.`,
    },
  ];

  return (
    <>
      <div className="amenity">
        <Nav />
        <div className="amenity__wrapper ">
          <div className="amenity__title">
            <h5>The little things matter</h5>
            <h2>Hotel Amenities</h2>
            <p>
              We know how something others consider redundant might be important
              for you. That's why we've added as many amenities we can for every
              room we have at Kababa Resort.
            </p>
          </div>
        </div>
      </div>
      <div className="amenities__rooms">
        <div className="amenity__wrapper">
          <div className="amenity__content">
            <div className="amenity__category">
              <h5>Everyday Ease</h5>
              <h2>Room amenities</h2>
              <p>
                Kababa Resort offers a large number of Room amenities that will
                ease up your stay when you're on your vacation. Here's what you
                always get when you stay here.
              </p>
            </div>
            <div className="amenity__items grid3">
              {roomAmenities.map(({ icon, title, desc }, idx) => (
                <div className="amenity__item" key={idx}>
                  <div className="amenity__icon">{icon}</div>
                  <h3>{title}</h3>
                  <span />
                  <p>{desc}</p>
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>
      <div className="amenities__hotel">
        <div className="amenity__wrapper">
          <div className="amenity__content">
            <div className="amenity__category amenity__category2">
              <h5>Outside Ease</h5>
              <h2>Additional Services</h2>
              <p>
                But the real things start outside, when you step out of your
                room. We'll help you to rest, take care of your luggage, or your
                clothing, or provide you with a guid when needed.
              </p>
            </div>
            <div className="amenity__items grid3">
              {hotelAmenities.map(({ icon, title, desc }, idx) => (
                <div className="amenity__item" key={idx}>
                  <div className="amenity__icon">{icon}</div>
                  <h3>{title}</h3>
                  <span />
                  <p>{desc}</p>
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default Amenities;
