import React, { useEffect, useState } from "react";
import { MdPets } from "react-icons/md";

import {
  FaBed,
  FaWifi,
  FaTv,
  FaCoffee,
  FaBath,
  FaShower,
  FaLifeRing,
} from "react-icons/fa";
import "./style.css";
import { dataAmenities1, dataAmenities2 } from "../../../data/data";
import { Link } from "react-router-dom";

function Amenities() {
  const [amenities1, setAmenities1] = useState([]);
  const [amenities2, setAmenities2] = useState([]);

  useEffect(() => {
    setAmenities1(dataAmenities1);
    setAmenities2(dataAmenities2);
  }, []);

  const icons = [
    <FaBed />,
    <FaWifi />,
    <FaTv />,
    <FaCoffee />,
    <FaBath />,
    <FaShower />,
    <FaLifeRing />,
  ];

  return (
    <div className="amenities">
      <div className="amenities__inner">
        <h1>General Amenities</h1>
        <div className="amenities__icons">
          {icons.map((icon, idx) => (
            <div key={idx} className="amenities__icon">
              {icon}
            </div>
          ))}
        </div>
        <div className="amenities__container">
          <ul className="amenities__wrapper">
            {amenities1?.map((amenity, idx) => (
              <li key={idx}>{amenity}</li>
            ))}
          </ul>
          <ul className="amenities__wrapper">
            {amenities2?.map((amenity, idx) => (
              <li key={idx}>{amenity}</li>
            ))}
          </ul>
        </div>

        <div className="amenities__footer">
          <p>
            <strong>
              <MdPets />
            </strong>{" "}
            Pets are NOT allowed
          </p>

          <Link to="/amenities" className="fancy-link">
            Read More
          </Link>
        </div>
      </div>
    </div>
  );
}

export default Amenities;
