import React from "react";
import {
  FaUser,
  FaChild,
  FaBed,
  FaEye,
  FaCropAlt,
  FaCalendar,
  FaCalendarAlt,
} from "react-icons/fa";
import Nav from "../../../component/Nav/Nav";
import "./style.css";

function Showcase({ room }) {
  const amenities = [
    {
      icon: <FaUser />,
      title: "adult",
      desc: "3",
    },
    {
      icon: <FaChild />,
      title: "children",
      desc: "2",
    },
    {
      icon: <FaCropAlt />,
      title: "acreage",
      desc: "18m2",
    },
    {
      icon: <FaBed />,
      title: "beds",
      desc: "1 doubled bed",
    },
    {
      icon: <FaEye />,
      title: "view",
      desc: "Balcony",
    },
  ];
  return (
    <>
      <div className="room__showcase">
        <Nav />
        <div className="room__showcase-inner"></div>
      </div>
      <div className="room__showcase-footer">
        <div className="room__showcase-amenities">
          {amenities.map(({ icon, title, desc }, idx) => (
            <div key={idx} className="room__showcase-amenitie">
              <span>
                {icon} {title}
              </span>
              <p>{desc}</p>
            </div>
          ))}
        </div>
        <div className="room__showcase-availability">
          <h5>Price</h5>
          <div className="room__showcase-price">
            <h1>
              <small>Kshs.</small>
              {room.price}
            </h1>
            <span>/ night</span>
          </div>
          <button className="check-availability">
            <FaCalendarAlt /> Check Availability This Room
          </button>
        </div>
      </div>
    </>
  );
}

export default Showcase;
