import React, { useEffect } from "react";
import { FaTv, FaBed, FaCalendar, FaWifi, FaClock } from "react-icons/fa";
import img1 from "../../../assets/imgs/homepage/rooms/room1.jpg";
import img2 from "../../../assets/imgs/homepage/rooms/room02.jpg";
import img3 from "../../../assets/imgs/homepage/rooms/room04.jpg";
import { Link } from "react-router-dom";
import "./style.css";

function Rooms({ rooms }) {
  return (
    <div className="rooms__list">
      {rooms.map(
        (
          {
            title,
            imgs,
            amenities,
            taxes,
            availability,
            room_type,
            price,
            slug,
          },
          idx
        ) => (
          <div key={idx} className="rooms__item">
            <div className="rooms__img">
              <img src={imgs[0]} alt="rooms" />
            </div>
            <div className="rooms__info">
              <h3>{title}</h3>
              {amenities.map(({ icon, title }, idx) => (
                <p key={idx}>
                  {icon} {title}
                </p>
              ))}
              <p className="rooms__available">
                <span>Availability: </span>
                {availability ? "Available" : "SOLD OUT"}
              </p>
              <p className="rooms__type">
                <span>Room Type: </span>
                {room_type}
              </p>
            </div>
            <div className="rooms__price">
              <h6>Best Price Guarantee/ Per Night</h6>
              <p>
                <small>KES</small>
                {taxes} + <span>TAXES</span>
              </p>
              <h3>
                <small>KES</small>
                {price}
              </h3>
              <Link to={slug} className="rooms__btn">
                <button>More Details</button>
              </Link>
            </div>
          </div>
        )
      )}
    </div>
  );
}

export default Rooms;
