import React from "react";
import { MdCheck } from "react-icons/md";
import { motion, AnimatePresence } from "framer-motion";
import "./style.css";

function CheckIn({ display }) {
  return (
    <>
      <AnimatePresence>
        {display && (
          <motion.div
            className="check__in"
            initial={{ y: "-50%", opacity: 0 }}
            animate={{ y: 0, opacity: 1 }}
            transition={{ duration: 1, type: "spring", stiffness: 100 }}
            exit={{ y: "-50%", opacity: 0 }}>
            <div className="check__inner">
              <div className="check__rate">
                <MdCheck />
                <div>
                  <h2>Rooms from Ksh. 2500</h2>
                  <p>Best rate Guaranteed</p>
                </div>
              </div>
              <form className="form__group">
                <div className="form__control">
                  <span>Check-in</span>
                  <input type="date" />
                </div>
                <div className="form__control">
                  <span>Check-out</span>
                  <input type="date" />
                </div>
                <div className="form__control">
                  <span>Rooms</span>
                  <select>
                    <option>1</option>
                    <option>2</option>
                    <option>3</option>
                  </select>
                </div>
                <div className="form__control">
                  <span>Adults</span>
                  <select>
                    <option>1</option>
                    <option>2</option>
                    <option>3</option>
                  </select>
                </div>
                <div className="form__control">
                  <span>Children</span>
                  <select>
                    <option>0</option>
                    <option>1</option>
                    <option>2</option>
                    <option>3</option>
                  </select>
                </div>
                <button>Check Rates</button>
              </form>
            </div>
          </motion.div>
        )}
      </AnimatePresence>
    </>
  );
}

export default CheckIn;
