import React from "react";
import img1 from "../../assets/imgs/birthday01.jpg";
import img2 from "../../assets/imgs/comfort05.jpg";
import img3 from "../../assets/imgs/drinks04.jpg";
import img4 from "../../assets/imgs/drinks01.jpg";
import Nav from "../../component/Nav/Nav";
import "./style.css";
import ImgSlider from "../../component/ImgSlider/ImgSlider";
import ScrollToTop from "../../utils/scrollToTop";

function Birthday() {
  const packageMenu = [
    "Private Dining Space of your choice, Complimentary Swimming",
    "Birthday Card",
    "10% discount on selected whiskey/ Champagne/ Brandy 10% on the Birthday Cake of your Choice",
    "Complimentary Meal for the birthday person for every 10 people booked",
  ];

  const bbqMenu = [
    "Salads",
    "Tossed Garden salad, Katchumbari, Chili Sambal",
    "Main Grills",
    "Barbecue Country Style Ribs, Marinated Chicken, Beef Steaks",
    "Choma Sausage Accompaniment Ugali, Boiled Maize, Traditional Mukimo, Potato wedges, Sautee green",
    "Sauces",
    "BBQ Sauce, Mushroom sauce, Pepper sauce Dessert Fresh fruit cut, Assorted Pastries",
  ];

  let imgs = [img1, img2, img3, img4];

  return (
    <>
      <ScrollToTop />
      <div className="birthday">
        <Nav />
        <div className="birthday__wrapper">
          <div className="birthday__title">
            <h2>Birthday Package</h2>
            <small>
              For an unforgettable birthday party for you and your family
            </small>
          </div>
          <div className="birthday__img">
            {/* <img src={imgs[0]} alt="" /> */}
            <ImgSlider img={imgs} />
          </div>
          <div className="birthday__details">
            <div className="birthday__header">
              <div className="birthday__left">
                <small>Suitable for:</small>
                <h6>Adults Birthdays</h6>
              </div>
              <div className="birthday__right">
                <h1>Package Details</h1>
              </div>
            </div>

            <div className="birthday__content">
              <div className="birthday__left">
                <small>Price:</small>
                <h6>Enquire</h6>
              </div>
              <div className="birthday__right">
                <p className="package-intro">
                  Celebrating a birthday? At #KababaResort we love to celebrate!
                  Book our birthday package and leave the planning to us, and
                  we'll leave the celebrating to you.
                </p>
                <div className="package-menu">
                  <p>Package Includes: </p>
                  <ul>
                    {packageMenu.map((item, idx) => (
                      <li key={idx}>{item}</li>
                    ))}
                  </ul>
                </div>
                <div className="package-bbq">
                  <h2>BBQ Menu</h2>
                  <ul>
                    {bbqMenu.map((item, idx) => (
                      <li key={idx}>{item}</li>
                    ))}
                  </ul>
                </div>
                <h4 className="package-price">@ 1,600/-</h4>
                <p>
                  Please contact us for package enhancements and reservations
                </p>
                <button className="check-availability">
                  call us +254 795772333
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default Birthday;
