import React, { useEffect, useState } from "react";
import { dataLinks } from "../../data/data";
import Footer from "../../layouts/Footer";
import {
  FaFacebookF,
  FaTwitter,
  FaYoutube,
  FaInstagram,
  FaGooglePlus,
  FaWhatsapp,
} from "react-icons/fa";

function FooterComp() {
  const [links, setLinks] = useState([]);
  useEffect(() => {
    setLinks(dataLinks);
  }, []);

  const socials = [
    {
      name: "facebook",
      icon: <FaFacebookF />,
      link: "www.facebook.com",
    },
    {
      name: "youtube",
      icon: <FaTwitter />,
      link: "www.facebook.com",
    },
    {
      name: "whatsapp",
      icon: <FaWhatsapp />,
      link: "www.whatsapp.com",
    },
    {
      name: "twitter",
      icon: <FaYoutube />,
      link: "www.facebook.com",
    },
    {
      name: "instagram",
      icon: <FaInstagram />,
      link: "www.instagram.com",
    },
    {
      name: "google-plus",
      icon: <FaGooglePlus />,
      link: "www.googleplus.com",
    },
  ];
  return <Footer socials={socials} links={links} />;
}

export default FooterComp;
