import React from "react";
import Showcase from "../../component/Showcase/Showcase";
import img from "../../assets/imgs/facilities/facility.jpg";
import Facilities from "../../layouts/FacilitiesPage/Facilities/Facilities";
import ScrollToTop from "../../utils/scrollToTop";
import Nearby from "../../layouts/FacilitiesPage/NearBy/Nearby";

function FacilitiesComp() {
  return (
    <>
      <ScrollToTop />
      <div>
        <Showcase img={img} route="Facilities" />
        <Facilities />
        <Nearby />
      </div>
    </>
  );
}

export default FacilitiesComp;
