import React from "react";
import Showcase from "../../component/Showcase/Showcase";
import img from "../../assets/imgs/homepage/meetings/meeting1.jpg";
import Meets from "../../layouts/Meetings/Meets";
import ScrollToTop from "../../utils/scrollToTop";

function MeetingComp() {
  return (
    <div>
      <ScrollToTop />
      <Showcase
        img={img}
        route="Meeting &amp; Conference"
        desc="Clutter-Free meeting spaces and efficient facilities"
      />
      <Meets />
    </div>
  );
}

export default MeetingComp;
