import React, { useEffect, useState } from "react";
import { useLocation } from "react-use";
import ScrollToTop from "../../utils/scrollToTop";
import { dataRooms } from "../../data/data";
import Showcase from "../../layouts/RoomPage/Showcase/Showcase";
import RoomInfo from "../../layouts/RoomPage/RoomInfo/RoomInfo";
import Amenities from "../../layouts/RoomPage/Amenities/Amenities";
// import Showcase from "../../component/Showcase/Showcase";

function RoomComp() {
  const [rooms, setRooms] = useState([]);
  const [room, setRoom] = useState([]);
  const { pathname } = useLocation();

  useEffect(() => {
    setRooms(dataRooms);

    rooms.map((room) => {
      if (room.slug !== pathname) return;
      if (room.slug === pathname) {
        setRoom(room);
      }
    });

    // let item = rooms.filter((room) => room.slug === pathname);
    // item = Object.values(item);
    // setRoom(item);
  }, [rooms, pathname]);

  document.title = `Kababa | ${room.title}`;

  return (
    <div>
      <ScrollToTop />
      <Showcase room={room} />
      <RoomInfo room={room} />
      <Amenities />
    </div>
  );
}

export default RoomComp;
