import React from "react";
import Title from "../../../component/Title/Title";
import "./style.css";
import img1 from "../../../assets/imgs/birthday01.jpg";
import img2 from "../../../assets/imgs/birthday02.jpg";
import img3 from "../../../assets/imgs/birthday03.jpg";
import { Link } from "react-router-dom";

function Offers() {
  const offers = [
    {
      img: img1,
      title: "Birthday Getaway",
      subtitle: "Celebrating a Bithday?",
      desc: `At #KababaResort we love to celebrate! Book our birthday package
      and leave the planning to us, and we'll leave the celebrating to
      you. `,
      desc2: "Contact us for more information.",
      slug: "/drinks-&-dines/adults",
      price: "1,600",
    },
    {
      img: img2,
      title: "Happy Birthday Party",
      subtitle: "for the kids",
      desc: `If you want to arrange an unforgettable birthday party for you kids, 
      come to #KababaResort. We have the perfect package to spoil them. You can also order 
      your child's bithday cake.`,
      slug: "/drinks-&-dines/kids",
      price: "1,000",
    },
    {
      img: img3,
      title: "Stay Longer, Save More",
      subtitle: "Celebrating a Bithday?",
      desc: `We have decorated rooms, beds so soft that you'll be tempted to stay just one extra night.
      Book directly from our website, get amazing deals on rooms and save!`,
      slug: "/accommodation",
      price: "4,600",
    },
  ];

  return (
    <div className="offers">
      <div className="offers__wrapper">
        <Title
          title="Reward yourself with these sensational offers"
          subtitle="Special offers on dining"
        />
        <div className="offers__content">
          {offers.map(
            ({ img, title, subtitle, desc, desc2, slug, price }, idx) => (
              <div key={idx} className="offer__item">
                <Link to={slug}>
                  <img src={img} alt="Offer Item" />
                </Link>
                <div className="offer__info">
                  <h3>{title}</h3>
                  <h5>{subtitle}</h5>
                  <p>{desc}</p>
                  {desc2 && <p>{desc2}</p>}
                </div>
                <div className="offer__footer">
                  <p>
                    from kes <span>{price}/-</span>
                  </p>
                  <Link to={slug} className="fancy-link">
                    Read More
                  </Link>
                </div>
              </div>
            )
          )}
        </div>
      </div>
    </div>
  );
}

export default Offers;
