import React from "react";
import { Link } from "react-router-dom";

import meets1 from "../../assets/imgs/homepage/meetings/meets.jpg";
import meets2 from "../../assets/imgs/homepage/meetings/meets01.jpg";
import meets3 from "../../assets/imgs/homepage/meetings/meets2.jpg";
import meets4 from "../../assets/imgs/homepage/meetings/meets3.jpg";
import meets5 from "../../assets/imgs/homepage/meetings/meet01.jpg";
import meets6 from "../../assets/imgs/homepage/meetings/meet03.jpg";
import meets7 from "../../assets/imgs/homepage/meetings/meet05.jpg";
import meets8 from "../../assets/imgs/homepage/meetings/meet06.jpg";

import meets02 from "../../assets/imgs/homepage/meetings/meet01.jpg";
import img1 from "../../assets/imgs/group01.jpg";
import img2 from "../../assets/imgs/group02.jpg";
import "./style.css";
import Title from "../../component/Title/Title";
import ImgSlider from "../../component/ImgSlider/ImgSlider";

function Meets() {
  const fullDay = [
    "Meeting Room",
    "Mid-morning Tea, Coffee and Biting",
    "Lunch including a soft drink (water or soda)",
    "Two half litre bottles of water (one in the morning & one afternoon)",
    "Dedicated notpads & pens",
    "Mints on the tables",
    "Flipchart paper and two felt pens(of different colors)",
    "Overhead projector and screen",
    "Wi-Fi access within the hotel standard bandwidth",
    "Car parking",
    "P.A system",
  ];

  const halfDay = [
    "Meeting Room",
    "Mid-morning Tea, Coffee and Biting",
    "Lunch including a soft drink (water or soda)",
    "One half litre bottles of water",
    "Dedicated notpads & pens",
    "Mints on the tables",
    "Flipchart paper and two felt pens(of different colors)",
    "Overhead projector and screen",
    "Wi-Fi access within the hotel standard bandwidth",
    "Car parking",
    "P.A system",
  ];

  const meets01 = [meets1, meets2, meets3, meets4];
  const meets02 = [meets5, meets6, meets7, meets8];

  const offer = [
    {
      title: "Group Bookings",
      img: img1,
      subtitle: "Accommodation for cast and crew",
      desc: `
    Shooting out on location? Need rooms but not event space?
                Ideally located at the heart of Siaya, Kababa Resort is the
                perfect ome for your meeting, or planning a glamorous getaway
                with family and friends, our sales team can help you find the
                best rate on rooms for your group.
    `,
    },
    {
      title: "Corporate Parties",
      img: img2,
      subtitle: "Celebrate in style",
      desc: `
    Whatever your occasion, however formal or relaxed, you set the tone and we'll be right there with you.
    Whether it's a black-tie event with 200 guests, a corporate Christmas party or a corporate event on a 
    shoestring; our Event Co-ordinators are at hand to help make it a great Corporate party.
    `,
    },
  ];

  return (
    <div className="meets">
      <div className="meets__wrapper">
        <div className="meets__item grid">
          <ImgSlider img={meets01} />
          <div className="meets__info">
            <div className="meets__content">
              <h5>Conferencing</h5>
              <h2>Meetings made easy</h2>
              <p>
                Our conference facilities have the latest technology facilities
                and amenities to hold your meetings. They can hold upto
                110people. Our well-kept grounds gives you the perfect
                atmosphere to hold your garden parties or weddings.
              </p>
              <Link to="/meeting/packages" className="fancy-link">
                Conference packages
              </Link>
            </div>
          </div>
        </div>
        <div className="meets__item grid">
          <div className="meets__info">
            <div className="meets__content">
              <h5>board rooms</h5>
              <h2>engage. get inspired</h2>
              <p>
                We know that business meetings should be confidential, for that
                reason our board room is built to give you the privacy you need.
                It is fitted with the latest facilities and amenities;
                teleconferencing and a 55-inch TV screen for your presentations.
              </p>
            </div>
          </div>
          <ImgSlider img={meets02} />
        </div>
      </div>
      <div className="group__offers">
        <Title
          subtitle="Group offers"
          title="Unparalleled experiences at exclusive rate"
        />
        <div className="group__offer-wrapper grid">
          {offer.map(({ title, subtitle, img, desc }, idx) => (
            <div key={idx} className="group__offer-item">
              <img src={img} alt="" />
              <div className="group__content">
                <h3>{title}</h3>
                <small>{subtitle}</small>
                <p>{desc}</p>
              </div>
              <div className="group__footer">
                <p>
                  call us now:<span>(254)795 772333</span>
                </p>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
}

export default Meets;
