import React from "react";
import img1 from "../../assets/imgs/kidbirthday.jpg";
import img5 from "../../assets/imgs/gallery03.jpg";
import img2 from "../../assets/imgs/birthday02.jpg";
import img3 from "../../assets/imgs/(20+) Facebook(40).jpg";
import img4 from "../../assets/imgs/(20+) Facebook(37).jpg";
import Nav from "../../component/Nav/Nav";
import "./style.css";
import ImgSlider from "../../component/ImgSlider/ImgSlider";
import ScrollToTop from "../../utils/scrollToTop";

function Birthday() {
  const packageMenu = [
    "Trampoline, swimming, bouncing castle",
    "Birthday cards",
    "A Birthday Welcome Sign at our Lobby",
    "Complimentary Meal for the birthday kid for every 10 booked",
  ];

  const bbqMenu = [
    "Salads",
    "Tossed Garden salad, Katchumbari, Chili Sambal",
    "Main Grills",
    "Barbecue Country Style Ribs, Marinated Chicken, Beef Steaks",
    "Choma Sausage Accompaniment Ugali, Boiled Maize, Traditional Mukimo, Potato wedges, Sautee green",
    "Sauces",
    "BBQ Sauce, Mushroom sauce, Pepper sauce Dessert Fresh fruit cut, Assorted Pastries",
  ];

  let imgs = [img1, img2, img5, img3, img4];

  return (
    <>
      <ScrollToTop />
      <div className="birthday">
        <Nav />
        <div className="birthday__wrapper">
          <div className="birthday__title">
            <h2>Birthday Package for Kids</h2>
            <small>For an unforgettable birthday party for your kids</small>
          </div>
          <div className="birthday__img">
            {/* <img src={imgs[0]} alt="" /> */}
            <ImgSlider img={imgs} />
          </div>
          <div className="birthday__details">
            <div className="birthday__header">
              <div className="birthday__left">
                <small>Suitable for:</small>
                <h6>Kids Birthdays</h6>
              </div>
              <div className="birthday__right">
                <h1>Package Details</h1>
              </div>
            </div>

            <div className="birthday__content">
              <div className="birthday__left">
                <small>Price:</small>
                <h6>Enquire</h6>
              </div>
              <div className="birthday__right">
                <p className="package-intro">
                  If you want to arrange an unforgettable birthday party for
                  your kids, come to #KababaResort. We have the perfect package
                  to spoil them:) You can also order your child's birthday cake
                  with us and get it delivered to schools within Siaya town.
                </p>
                <div className="package-menu">
                  <p>Package Includes: </p>
                  <ul>
                    {packageMenu.map((item, idx) => (
                      <li key={idx}>{item}</li>
                    ))}
                  </ul>
                </div>
                <div className="package-bbq">
                  <h2>Kids Menu</h2>
                  <p>
                    Meat balls in BBQ Sauce, Mini margarita Pizza, Chicken
                    Nuggets with sweet mayo, Mini hot dog, Chips, Mashed potato
                    with baked beans on the side.
                  </p>
                  <h2>Sweet Corner</h2>
                  <p>
                    Marshmallow, Chocolate fudge, Black forest cake, Ice cream
                    Sundae
                  </p>
                </div>
                <h4 className="package-price">@ 1,000/-</h4>
                <p>
                  Please contact us for package enhancements and reservations
                </p>
                <button className="check-availability">
                  call us +254 795772333
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default Birthday;
