import React from "react";
import { IoIosCheckmark } from "react-icons/io";
import "./style.css";

function Amenities() {
  const amenities = [
    "Private bathroom",
    "Two towels",
    "Hot/Cold Shower & Bathtub",
    "2 pair of slippers",
    "Free Wifi",
    "Free Breakfast",
    "Writing Desk & Chair",
    "Bottled Mineral Water",
  ];
  return (
    <div className="room__amenities">
      <div className="room__amenities-wrapper">
        <h3>Room Amenities</h3>
        <div className="room__amenities-content">
          {amenities.map((item, idx) => (
            <p key={idx}>
              <IoIosCheckmark /> {item}
            </p>
          ))}
        </div>
      </div>
    </div>
  );
}

export default Amenities;
