import React, { useEffect, useState } from "react";
import { dataRooms } from "../../data/data";
import img from "../../assets/imgs/homepage/rooms/slider011.jpg";

import Amenities from "../../layouts/RoomsPage/Amenities/Amenities";
import Rooms from "../../layouts/RoomsPage/Rooms/Rooms";
import ScrollToTop from "../../utils/scrollToTop";
import Showcase from "../../component/Showcase/Showcase";
import Overview from "../../component/Overview";

import "./style.css";

function RoomsComp() {
  const [rooms, setRooms] = useState([]);

  useEffect(() => {
    setRooms(dataRooms);
  });

  document.title = "Kababa | Rooms";
  const desc = `
  Chose to stay with us and enjoy modern home comfort in a traditional setting. Whether you are looking 
  for a short weekend break or a longer holiday, we offer a range of packages that we think cater for all.
  `;

  return (
    <div>
      <ScrollToTop />
      <div className="rooms">
        <Showcase
          route="Accommodation"
          img={img}
          desc="Enjoy both modern &amp; traditional settings."
        />
        {/* <FilterRooms /> */}
        <Overview desc={desc} />
        <Rooms rooms={rooms} />
        <Amenities />
      </div>
    </div>
  );
}

export default RoomsComp;
