import React, { useEffect, useState } from "react";
import { motion } from "framer-motion";
import "./style.css";

function Socials({ socials }) {
  const [visible, setVisible] = useState(false);

  useEffect(() => {
    const socials = document.querySelector(".socials");
    const socialsInner = socials.querySelector(".socials__inner");
    const options = { threshold: 0.4, rootMargin: "0px 0px -150px 0px" };

    const observer = new IntersectionObserver((entries) => {
      entries.forEach((entry) => {
        if (!entry.isIntersecting) {
          return;
        } else {
          setVisible(entry.isIntersecting);
          // socialsInner.class;
          console.log(socialsInner);
          observer.unobserve(entry.target);
        }
      });
    }, options);

    observer.observe(socials);
  }, []);

  return (
    <section className="socials">
      {visible && (
        <motion.div
          className="socials__inner"
          initial={{ y: -70, opacity: 0 }}
          animate={{ y: 0, opacity: 1 }}
          transition={{
            duration: 1,
            delay: 1,
            type: "spring",
            stiffness: 120,
          }}>
          <h1>Kababa Social Moments</h1>
          <motion.h3
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            transition={{
              delay: 2,
              type: "spring",
              stiffness: 120,
              when: "afterParent",
            }}>
            #kababahotel
          </motion.h3>
          <ul>
            {socials?.map(({ icon, link }, idx) => (
              <li key={idx}>
                <motion.a
                  href={link}
                  target="_blank"
                  whileHover={{
                    scale: [1, 1.1, 1, 1.1],
                  }}>
                  {icon}
                </motion.a>
              </li>
            ))}
          </ul>
        </motion.div>
      )}
    </section>
  );
}

export default Socials;
