import React, { useState, useEffect, useRef } from "react";
import { MdChevronLeft, MdChevronRight } from "react-icons/md";
import { Link } from "react-router-dom";
import img from "../../../assets/imgs/homebg1.jpg";
import img2 from "../../../assets/imgs/homebg2.jpg";
import img3 from "../../../assets/imgs/homebg3.jpg";
import img4 from "../../../assets/imgs/homebg4.jpg";
import img5 from "../../../assets/imgs/homebg5.jpg";
import { motion, AnimatePresence } from "framer-motion";
import { dataLinks } from "../../../data/data";

import "./style.css";
import Nav from "../../../component/Nav/Nav";

function ShowcaseUI({}) {
  const [current, setCurrent] = useState(0);

  const slides = [
    {
      img: img,
      title: "Truly",
      span: "remarkable",
      info: `Lorem ipsum dolor sit amet consectetur 
      adipisicing elit. Aspernatur delectus quas 
      eaque sapiente deserunt id molestiae magni 
      earum explicabo illo.`,
    },
    {
      img: img2,
      title: "Best customer",
      span: "care",
      info: `Lorem ipsum dolor sit amet consectetur 
      adipisicing elit. Aspernatur delectus quas 
      eaque sapiente deserunt id molestiae magni 
      earum explicabo illo.`,
    },
    {
      img: img3,
      title: "Peaceful",
      span: "environment",
      info: `Lorem ipsum dolor sit amet consectetur 
      adipisicing elit. Aspernatur delectus quas 
      eaque sapiente deserunt id molestiae magni 
      earum explicabo illo.`,
    },
  ];

  const length = slides.length;
  const timeout = useRef(null);

  useEffect(() => {
    const nextSlide = () => {
      setCurrent((current) => (current === length - 1 ? 0 : current + 1));
    };
    timeout.current = setTimeout(nextSlide, 10000);
    return function () {
      if (timeout.current) {
        clearTimeout(timeout.current);
      }
    };
  }, [current, length]);

  const nextSlide = () => {
    if (timeout.current) {
      clearTimeout(timeout.current);
    }

    setCurrent(current === length - 1 ? 0 : current + 1);
  };

  const prevSlide = () => {
    if (timeout.current) {
      clearTimeout(timeout.current);
    }

    setCurrent(current === 0 ? length - 1 : current - 1);
  };

  if (!Array.isArray(slides) || slides.length <= 0) {
    return null;
  }

  const intro = (
    <div className="hero__inner">
      <div className="hero__left">
        <h2>Welcome to Kababa Resort</h2>
        <div className="hero__intro">
          <p>Get Everything &amp; More to:</p>
          <h3>live. work. play</h3>
        </div>
      </div>
      <div className="hero__right"></div>
    </div>
  );

  const content = (title, span, info) => {
    return (
      <motion.div
        className="slide__content"
        initial={{ opacity: 0, y: 100 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 1, delay: 2 }}>
        <h2>
          {title} <span>{span}</span>
        </h2>
        <p>{info}</p>
      </motion.div>
    );
  };

  return (
    <div className="hero">
      <Nav />
      <div className="hero__wrapper">
        {slides.map(({ img, title, span, info }, idx) => (
          <div className="sliders" key={idx}>
            <AnimatePresence exitBeforeEnter>
              {idx === current && (
                <motion.div
                  className="slide"
                  initial={{ opacity: 0, scale: 1.1 }}
                  animate={{ opacity: 1, scale: 1 }}
                  exit={{ opacity: 0, scale: 1.1 }}
                  transition={{ duration: 2 }}>
                  <img src={img} alt="" />
                  {content(title, span, info)}
                </motion.div>
              )}
            </AnimatePresence>
          </div>
        ))}
        <div className="sliders__nav">
          <div className="slider__btn">
            <MdChevronLeft onClick={prevSlide} />
          </div>
          <div className="slider__btn">
            <MdChevronRight onClick={nextSlide} />
          </div>
        </div>
      </div>
    </div>
  );
}

export default ShowcaseUI;
