import React, { useState, useEffect } from "react";
import img1 from "../../../assets/imgs/homepage/rooms/room1.jpg";
import img2 from "../../../assets/imgs/homepage/rooms/room05.jpg";
import img3 from "../../../assets/imgs/homepage/rooms/room04.jpg";
import img6 from "../../../assets/imgs/homepage/rooms/room02.jpg";
import "./style.css";
import { FaCalendar, FaCalendarAlt, FaChild, FaUserAlt } from "react-icons/fa";

function RoomInfo({ room }) {
  const [current, setCurrent] = useState(null);
  const imgs = room?.imgs;
  console.log(room);

  useEffect(() => {
    {
      imgs && setCurrent(imgs[0]);
    }

    {
      imgs && document.querySelector(".room__imgs").classList.add("active");
    }
  }, [room]);

  const setActive = (e) => {
    document.querySelectorAll(".room__imgs").forEach((img) => {
      img.classList.remove("active");
    });

    e.target.classList.add("active");
    setCurrent(e.target.src);
  };

  return (
    <div className="room__info">
      <div className="room__info-wrapper">
        <div className="room__info-content">
          <div className="room__info-imgs">
            <div className="room__info-img">
              <img src={current} alt="" />
            </div>

            <div className="room__info-gallery">
              {imgs?.map((img, idx) => (
                <img
                  onClick={(e) => setActive(e)}
                  key={idx}
                  src={img}
                  alt=""
                  className={`room__imgs`}
                />
              ))}
            </div>
          </div>
        </div>

        <div className="room__info-book">
          <div className="room__info-cart">
            <h3>Your Booking</h3>
            <h5>Your cart is empty</h5>
          </div>
          <div className="room__info-reservation">
            <h1>Your Reservation</h1>
            <form>
              <div className="form__group">
                <FaCalendarAlt />
                <input type="date" placeholder="Arrival Date" />
              </div>
              <div className="form__group">
                <FaCalendarAlt />
                <input type="date" placeholder="Departure Date" />
              </div>
              <div className="form__group">
                <FaUserAlt />
                <select>
                  <option value="adults">Adults</option>
                  <option value="1">1</option>
                  <option value="2">2</option>
                  <option value="3">3</option>
                </select>
              </div>
              <div className="form__group">
                <FaChild />
                <select>
                  <option value="children">Children</option>
                  <option value="1">1</option>
                  <option value="2">2</option>
                  <option value="3">3</option>
                </select>
              </div>
              <button className="check-availability">
                <FaCalendarAlt /> Check Availability
              </button>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
}

export default RoomInfo;
