import React, { useEffect, useState, useRef } from "react";
import { MdChevronLeft, MdChevronRight } from "react-icons/md";
import { motion, AnimatePresence } from "framer-motion";
import "./style.css";

function ImgSlider({ img }) {
  const [current, setCurrent] = useState(0);
  const length = img.length;
  const timeout = useRef(null);

  useEffect(() => {
    const nextSlide = () => {
      setCurrent((current) => (current === length - 1 ? 0 : current + 1));
    };

    timeout.current = setTimeout(nextSlide, 6000);

    return function () {
      if (timeout.current) {
        clearTimeout(timeout.current);
      }
    };
  }, [current, length]);

  const nextSlide = () => {
    if (timeout.current) {
      clearTimeout(timeout.current);
    }
    setCurrent(current === length - 1 ? 0 : current + 1);
    // console.log(current);
  };
  const prevSlide = () => {
    if (timeout.current) {
      clearTimeout(timeout.current);
    }
    setCurrent(current === 0 ? length - 1 : current - 1);
    // console.log(current);
  };

  return (
    <div className="img__slider">
      {img.map((item, idx) => (
        <div key={idx} className="img__item">
          <AnimatePresence exitBeforeEnter>
            {idx === current && (
              <>
                <motion.img
                  initial={{ scale: 1.1, opacity: 0 }}
                  animate={{ scale: 1, opacity: 1 }}
                  exit={{ scale: 1.1, opacity: 0 }}
                  transition={{ duration: 1 }}
                  src={item}
                  alt=""
                />
              </>
            )}
          </AnimatePresence>
          <div className="img__nav">
            <MdChevronLeft onClick={prevSlide} />
            <MdChevronRight onClick={nextSlide} />
          </div>
        </div>
      ))}
    </div>
  );
}

export default ImgSlider;
