import { Link } from "react-router-dom";
import React from "react";
import { FaChevronRight } from "react-icons/fa";
import { dataLinks } from "../../data/data";
import { useLocation } from "react-use";
import "./style.css";
import Nav from "../Nav/Nav";

function Showcase({ img, route, desc }) {
  const { pathname } = useLocation();

  const showcase = (
    <>
      <div className="hero__navbar">
        <div className="hero__navbar-wrapper">
          <Link to="/" className="hero__logo">
            <h1>K`</h1>
            <div>
              <h2>ababa</h2>
              <h2>Resort</h2>
            </div>
          </Link>
          <ul className="hero__menu">
            {dataLinks?.map(({ link, route }, idx) => (
              <li key={idx}>
                <Link
                  className={`navlinks ${pathname === route && "active"}`}
                  to={route}>
                  {link}
                </Link>
              </li>
            ))}
          </ul>
        </div>
      </div>
      <div className="facilities__showcase">
        <div className="facilities__showcase__img">
          {img && <img src={img} alt="Facilities" />}
          <div className="facilities__showcase__inner">
            <h2>{route}</h2>
            {desc && <p>{desc}</p>}
          </div>
        </div>
      </div>
    </>
  );

  return (
    <div className="facilities__showcase">
      <Nav />
      <div className="facilities__showcase__img">
        {img && <img src={img} alt="Facilities" />}
        <div className="facilities__showcase__inner">
          <h2>{route}</h2>
          {desc && <p>{desc}</p>}
        </div>
      </div>
    </div>
  );
}

export default Showcase;
