import React from "react";
import { Link } from "react-router-dom";
import { dataLinks } from "../../data/data";
import { useLocation } from "react-use";
import { motion } from "framer-motion";

function Nav() {
  const { pathname } = useLocation();
  return (
    <motion.div
      className="hero__navbar"
      initial={{ y: "-100%", opacity: 0 }}
      animate={{ y: 0, opacity: 1 }}
      transition={{ duration: 1, delay: 0 }}>
      <div className="hero__navbar-wrapper">
        <Link to="/" className="hero__logo">
          <h1>K`</h1>
          <div>
            <h2>ababa</h2>
            <h2>Resort</h2>
          </div>
        </Link>
        <ul className="hero__menu">
          {dataLinks?.map(({ link, route }, idx) => (
            <motion.li
              key={idx}
              initial={{ y: -100, opacity: 0 }}
              animate={{ y: 0, opacity: 1 }}
              transition={{ duration: 1, delay: idx / 2 }}>
              <Link
                className={`navlinks ${pathname === route && "active"}`}
                to={route}>
                {link}
              </Link>
            </motion.li>
          ))}
        </ul>
      </div>
    </motion.div>
  );
}

export default Nav;
