  document.title = "Kababa Resort";

  return (
    <main>
      <ScrollToTop />
      <Showcase slides={slides} />
      <Overview overview={overview} />
      <Categories
        categories={categories}
        prevSlide={prevSlide}
        nextSlide={nextSlide}
      />
      <Experience />
      {/* <Reasons reasons1={reasons1} reasons2={reasons2} />
      <Testimonials /> */}
      <Socials socials={socials} />
    </main>
  );
